

/**
 * formatime translate it to some forms like  
 * 
 * @param {any} tar 
 */
function formatime (tar) {
  const now = (new Date()).valueOf();
  const pre = (new Date(tar)).valueOf();
  const offset = Math.floor((now - pre) / 1000);
  if (offset < 60) return '';
  else if (offset < 3600) return `${Math.floor(offset / 60)}`;
  else if (offset < 3600 * 24) return `${Math.floor(offset / 3600)}`;
  else if (offset < 3600 * 24 * 30) return `${Math.floor(offset / (3600 * 24))}`;
  else if (offset < 3600 * 24 * 30 * 12) return `${Math.floor(offset / (3600 * 24 * 30))}`;
  else return `${Math.floor(offset / (3600 * 24 * 30 * 12))}`;
}

/**
 * translate the time to some forms like 1.7
 * 
 * @param {any} tar 
 * @returns 
 */

function toDetailedTime (tar) {
  const now = (new Date()).valueOf();
  const pre = (new Date(tar)).valueOf();
  const offset = now - pre;
  if (offset < 3600 * 24 * 30 * 1000) return `${Math.floor(offset / (3600 * 24 * 1000))}`;
  else if (offset < 3600 * 24 * 30 * 12 * 1000) return `${Math.floor(offset / (3600 * 24 * 30 * 1000))}`;
  else return `${(offset / (3600 * 24 * 30 * 1000 * 12)).toFixed(1)}`;
}

/**
 * 
 * 
 * @param {any} fn 
 * @param {any} atleast 
 * @returns 
 */
function throttle (fn, delay) {
  var previous = null;

  return function () {
    var now = +new Date();
    if ( !previous ) previous = now;
    if ( now - previous > delay ) {
      fn();
      // 
      previous = now;
    }
  }
};

export {
  formatime,
  toDetailedTime,
  throttle
};


